@import 'global.scss';

:host {
  /**
   * @prop --slides-animation-duration: The duration of the slide change animations.
   * @prop --slides-slide-fade-transition: The transition of the slide from active to default state.
   * @prop --slides-slide-active-fade-transition: The transition of the slide from default to active state.
   
   * @prop display: The display mode of the host element.
   */
  --slides-animation-duration: 200ms;
  
  --slides-slide-fade-transition:
    opacity var(--slides-animation-duration) ease-in-out,
    visibility 0ms linear var(--slides-animation-duration),
    position 0ms linear var(--slides-animation-duration),
    left 0ms linear var(--slides-animation-duration);
    
  --slides-slide-active-fade-transition:
    opacity var(--slides-animation-duration) ease-in-out var(--slides-animation-duration),
    visibility 0ms linear var(--slides-animation-duration),
    position 0ms linear var(--slides-animation-duration),
    left 0ms linear var(--slides-animation-duration);

  display: block;
}

.container {
  width: 100%;
  overflow-x: hidden;
  position: relative;
}

.slide-flex {
  display: flex;
  flex-direction: row;
}

.slide-wrapper {
  width: 100%;
}

::slotted(*) {
  left: #{$off-screen};
  opacity: 0;
  position: absolute;
  transition: var(--slides-slide-fade-transition);
  visibility: hidden;
}

::slotted(.active) {
  left: 0;
  opacity: 1;
  position: unset;
  transition: var(--slides-slide-active-fade-transition);
  visibility: visible;
}
