import { Component, h, Element, State, Prop, Event, EventEmitter, Method, FunctionalComponent, Listen, Host, Watch } from '@stencil/core';
import { IPaginable } from '../../../interfaces/actions/IPaginable';
import { EventManager } from '../../../utils/actions/eventManager';
import { RWCActionHelper } from '../../../utils/actions/rwcActionHelper';

@Component({
  tag: 'rwc-slides',
  styleUrl: 'slides.scss',
  shadow: true
})
export class RwcSlides implements IPaginable {
  /**
   * The class name used for the active slide element.
   */
  static ACTIVE_SLIDE_CLASS = 'active';

  /**
   * The reference to the host element.
   */
  @Element() host: HTMLRwcSlidesElement;

  /**
   * Event manager used to manage component's event subscriptions.
   */
  public eventManager: EventManager = new EventManager();

  /**
   * List of the actions available for the Slides.
   */
  public actions: 'goTo'[] = ['goTo'];

  /**
   * The coroutine handling the slide animation.
   */
  private autoSlideCoroutine: NodeJS.Timer = null;

  /**
   * The reference to the element containing slides.
   */
  private slideContainer: HTMLSlotElement;

  /**
   * The reference to the slide elements.
   */
  private slides?: HTMLElement[];

  /**
   * Key used for initializing event listeners.
   */
  @Prop({ reflect: true }) rwcKey = RWCActionHelper.getDefaultComponentKey('slides');

  /**
   * The active slide on component load.
   */
  @Prop() defaultSlide: number = 0;

  /**
   * Enable auto slide change.
   */
  @Prop() autoSlide: boolean = false;

  /**
   * Auto slide change delay in milliseconds.
   */
  @Prop() autoSlideDelay: number = 5000;

  /**
   * Event fired when active slide has been changed.
   */
  @Event({ eventName: 'indexChange' }) changeEvent: EventEmitter<GoToEventDetail>;

  /**
   * Currently active tab.
   */
  @State() activeSlide: number = 0;
  
  @Watch('autoSlide')
  onAutoSlideChange(): void {
    this.init();
  }

  /**
   * Pauses the auto slide if mouse is inside the content.
   */
  @Listen('mouseenter')
  onMouseEnter(): void {
    clearInterval(this.autoSlideCoroutine);
  }

  /**
   * Resumes the auto slide if mouse is outside of the content.
   */
  @Listen('mouseleave')
  onMouseLeave(): void {
    if (this.autoSlide) {
      this.initAutoSlide();
    }
  }
  
  /**
   * Gets the currently active tab index in valid range.
   */
  private validateTargetIndex(value: number): number {
    if (value > this.slides.length - 1 || value < 0) {
      return 0;
    }
    
    return value;
  }

  /**
   * Initializes event listeners on render.
   */
  componentDidLoad(): void {
    this.init();
  }

  /**
   * Destroy component's event listeners before component is destroyed.
   */
  disconnectedCallback(): void {
    clearInterval(this.autoSlideCoroutine);
    this.eventManager.clearEventListeners();
  }

  /**
   * Initialize the component.
   */
  init(): void {
    this.eventManager.clearEventListeners();
    this.slides = this.slideContainer.assignedElements() as HTMLElement[];
    this.initDefaultSlide();
    this.initGoToEventListeners();
    clearInterval(this.autoSlideCoroutine);
    if (this.autoSlide) {
      this.initAutoSlide();
    }
  }

  /**
   * Initialize default tab.
   */
  initDefaultSlide(): void {
    this.activeSlide = this.validateTargetIndex(this.activeSlide);
    if (this.slides?.length > 0) {
      this.slides[this.activeSlide].classList.add(RwcSlides.ACTIVE_SLIDE_CLASS);
    }
  }

  /**
   * Initialize auto slide coroutine.
   */
  initAutoSlide(): void {
    clearInterval(this.autoSlideCoroutine);
    this.autoSlideCoroutine = setInterval(() => {
      this.goTo(this.validateTargetIndex(this.activeSlide + 1));
    }, this.autoSlideDelay);
  }

  /**
   * Change active tab to provided index and start indicator animation.
   */
  @Method()
  async goTo(targetIndex: number | CustomEvent): Promise<void> {
    const parsedIndex: number = this.validateTargetIndex(targetIndex instanceof CustomEvent ? Number.parseInt(targetIndex.detail.next) : targetIndex);
    
    if (this.activeSlide !== parsedIndex) {
      this.slides.forEach(slide => {
        slide.classList.remove(RwcSlides.ACTIVE_SLIDE_CLASS);
      });

      const originalEvent = targetIndex instanceof CustomEvent ? targetIndex : null;
      this.setActiveSlide(parsedIndex, originalEvent);
    }
  }

  /**
   * Set html and class properties for active slide and fire change event.
   */
  setActiveSlide(targetIndex: number, originalEvent?: CustomEvent): void {
    this.slides[targetIndex].classList.add(RwcSlides.ACTIVE_SLIDE_CLASS);
    this.changeEvent.emit(originalEvent?.detail ?? { origin: this.host, next: targetIndex, previous: this.activeSlide });
    this.activeSlide = targetIndex;
  }

  /**
   * Find elements by attribute name and component's key and add event listeners on click.
   */
  initGoToEventListeners(): void {
    RWCActionHelper.bindRWCActions(this, this.host, this.actions, this.rwcKey, 'indexChange');
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          class={ {
            container: true
          } }
        >
          <div class="slide-wrapper">
            <div class="slide-flex">
              <slot
                ref={ (slot: HTMLSlotElement) => this.slideContainer = slot }
                onSlotchange={ () => this.init() }
              />
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
